body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  padding-top : 100px;
}
.canvas-cont {
  margin: 80px 0; }
  .canvas-cont .axis path, .canvas-cont .axis-line {
    fill: none;
    stroke: #494949;
    shape-rendering: crispEdges; }

.svg-tooltip > text {
  z-index: 1000;
  width: 100px; }

.value-circle {
  width: 150px;
  height: 150px;
  display: flex;
  border-radius: 50%;
  border: 4px solid #fff;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px; }
  .value-circle h5 {
    font-weight: 500;
    margin: 0; }

.price-eval {
  display: flex;
  align-items: center;
  flex-direction: column; }

.similar-apartments {
  margin-top: 50px; }
  .similar-apartments h4 {
    text-align: center; }

.apartment-card {
  border: 1px solid #9a9a9a;
  border-radius: 5px;
  padding: 5px;
  font-size: 12px;
  font-weight: 500;
  margin: 30px 0; }
  .apartment-card .img-cont {
    height: 200px; }
    .apartment-card .img-cont img {
      width: 100%;
      max-height: 100%; }
  .apartment-card .details {
    margin-top: 10px; }

.apart-eval-details div {
  margin: 10px 0; }
  .apart-eval-details div > span {
    font-weight: 400; }

.sort-comp button {
  width: 100%; }

.sort-by {
  margin: 30px 0 10px;
  font-size: 18px; }

.form-control.select {
  height: 40px !important;
  border: 0;
  border-bottom: 1px solid #ced4da;
  border-radius: 0;
  padding-left: 0; }
  .form-control.select:focus {
    box-shadow: none;
    outline: none !important;
    border-color: #4285f4;
    border-width: 1.5px; }
  .form-control.select.invalid {
    border-color: #f44336;
    border-width: 1.5px; }

.basic-form {
  width: 500px;
  margin: auto;
  max-width: 100%; }

.form-cont .form-group, .form-cont .md-form {
  margin-bottom: 40px; }

.final-cta {
  margin: 50px 0;
  text-align: center; }

.select-cont {
  padding-right: 5px; }

.error-message {
  font-size: 12px;
  color: #f44336;
  position: absolute;
  opacity: 0;
  bottom: -20px;
  visibility: hidden;
  transition-duration: 400ms;
  transition-timing-function: ease-in-out; }

.input-cont {
  position: relative; }

.invalid-parent ~ .error-message {
  opacity: 1;
  visibility: visible; }

.progress-bar {
  height: 2px;
  position: fixed;
  z-index: 1000;
  top: 0;
  background: rebeccapurple;
  left: 0; }

.switch-button {
  margin-top: 15px;
  margin-bottom: 15px; }
  .switch-button button {
    width: 100%; }

